<template>
  <div>
    <template v-if="show_button">
      <b-card-body class="pb-2 mt-2 px-2 py-0 d-flex justify-content-center" v-if="!hasMicAccess || !hasCamAccess">
        <div class="sr-options w-100">
          <div class="align-items-baseline d-inline-flex justify-content-center p-0 video-tabs rounded p-2 bg-warning-soft w-100">
            <!-- Microphone &  Camera Settings -->

            <div class="align-items-center align-middle d-flex justify-content-between">
              <div class="d-flex justify-content-between align-items-center text-nowrap w-100">
                <h5 class="mb-0">
                  <span class="sr-team-name  ml-1 pl-1 mr-3">Microphone & Camera </span>
                </h5>
                <b-button variant="outline-primary grant-buttons" size="sm" @click="requestMediaDeviceAccess" class="small-2">Grant Access</b-button>
              </div>
            </div>
          </div>
        </div>
      </b-card-body>

      <b-card-body class="pb-2 mt-2 px-2 d-flex justify-content-center pt-0" v-if="!hasMicrophone || !hasWebcam">
        <div class="sr-options w-100">
          <div class="align-items-baseline align-middle d-inline-flex justify-content-center p-2 rounded bg-warning-soft w-100">
            <!-- Microphone Settings -->
            <div v-if="!hasMicrophone">
              <div class="align-items-center align-middle d-flex justify-content-between">
                <div class="d-flex justify-content-between align-items-center text-nowrap w-100">
                  <h5 class="mb-0">
                    <span class="sr-team-name"> No Microphone Detected</span>
                  </h5>
                </div>
                <!--  -->
              </div>
            </div>

            <span class="px-3">|</span>
            <!-- Camera Settings -->
            <div v-if="!hasWebcam">
              <div class="d-flex align-items-center justify-content-between">
                <div class="d-flex justify-content-between align-items-baseline  text-nowrap w-100">
                  <h5 class="mb-0">
                    <span class="sr-team-name">No Camera Detected</span>
                  </h5>
                </div>
              </div>
            </div>
          </div>
        </div>
      </b-card-body>
    </template>
    <!--Camera not support modal starts here-->
    <b-modal
      id="grant_permission"
      size="lg"
      ref="camera-not-support-modal"
      header-class="border-bottom-0 d-none"
      footer-class="border-top-0 pt-2"
      ok-variant="primary btn-sm px-4 py-1 mx-auto"
      body-class="py-3"
      ok-only
    >
      <div id="getAccess">
        <h3>Enable Camera & Audio permissions to record yourself.</h3>
        <p class="mb-2 pl-3">Please follow below steps</p>
        <template v-if="browser_type.browser == 'Chrome'">
          <div class="p-0 pt-4 px-3">
            <div>
              <div class="bg-lighter p-3 rounded">
                <ul>
                  <li>Click on camera icon in the address bar</li>
                  <li>Select always Allow to access camera and microphone</li>
                  <li>click on OK button below</li>
                </ul>
              </div>

              <p class="mt-2 mb-1">You can REMOVE i.e. DELETE camera permissions anytime on these page</p>
              <a href="chrome://settings/content/camera?search=camera" target="_blank">chrome://settings/content/camera?search=camera</a>
            </div>
            <div class="text-center mt-2"><img src="/img/ninjainterview/ninja-cap.gif" class="img-thumbnail" /></div>
          </div>
        </template>
        <template v-if="browser_type.browser == 'Firefox'">
          <div class="p-0 pt-4 px-3">
            <div>
              <div class="bg-lighter p-3 rounded">
                <ul>
                  <li>Click on the camera icon in the address bar</li>
                  <li>Click on the Blocked buttons next to the camera and microphone</li>
                  <li>Click on the OK button below</li>
                </ul>
              </div>
            </div>
          </div>
        </template>
        <template v-if="browser_type.browser == 'Edge'">
          <div class="p-0 pt-4 px-3">
            <div>
              <div class="bg-lighter p-3 rounded">
                <ul>
                  <li>Click on camera icon in the address bar</li>
                  <li>Select always Allow to access camera and microphone</li>
                  <li>Click on the OK button below</li>
                </ul>
              </div>
            </div>
          </div>
        </template>
        <template v-if="browser_type.browser == 'Safari'">
          <div class="p-0 pt-4 px-3">
            <div>
              <div class="bg-lighter p-3 rounded">
                <ul>
                  <li>Click Safari -> Preferences</li>
                  <li>Then click the Website tab</li>
                  <li>Select Camera. Find currently open Website, and set camera permissions to either Allow or Ask</li>
                  <li>Next,go to Microphone section and do the same thing</li>
                </ul>
              </div>
            </div>
          </div>
        </template>
      </div>
    </b-modal>

    <!--Camera not support modal ends here-->
  </div>
</template>
<script>
  export default {
    props: {
      browser_type: Object,
      show_button: {
        type: Boolean,
        default: true,
      },
    },
    data() {
      return {
        hasWebcam: false,
        isWebcamAlreadyCaptured: false,
        hasMicrophone: false,
        isMicrophoneAlreadyCaptured: false,
      };
    },
    created() {
      this.checkCameraAndAudio();
    },
    mounted() {
      this.$root.$on("bv::modal::hide", (bvEvent, modalId) => {
        if (modalId == "grant_permission") {
          this.checkCameraAndAudio();
          this.$emit("ok");
        }
      });
    },
    computed: {
      hasCamAccess() {
        if (this.hasWebcam && !this.isWebcamAlreadyCaptured) {
          return false;
        }
        return true;
      },
      hasMicAccess() {
        if (this.hasMicrophone && !this.isMicrophoneAlreadyCaptured) {
          return false;
        }
        return true;
      },
    },
    methods: {
      // Open modal
      open() {
        this.$refs["camera-not-support-modal"].show();
      },
      //Check camera and audio
      checkCameraAndAudio() {
        navigator.mediaDevices
          .enumerateDevices()
          .then((devices) => {
            const devicesk = JSON.parse(JSON.stringify(devices));
            devicesk.forEach((device) => {
              if (device.kind === "audioinput") {
                if (device.deviceId && device.label) {
                  this.isMicrophoneAlreadyCaptured = true;
                }
                this.hasMicrophone = true;
              } else if (device.kind === "videoinput") {
                if (device.deviceId && device.label) {
                  this.isWebcamAlreadyCaptured = true;
                }
                this.hasWebcam = true;
              }
            });
          })
          .then(() => {})
          // eslint-disable-next-line handle-callback-err
          .catch(function(err) {
            console.log(err);
          });
      },

      // Get for media device access
      async requestMediaDeviceAccess() {
        // Request for user media access.
        try {
          await navigator.mediaDevices.getUserMedia({ video: true, audio: true });
          console.log("User accepted");
          this.checkCameraAndAudio();
          this.$emit("ok");
        } catch (err) {
          console.log("err", err);
          this.open();
        }
      },
    },
  };
</script>
<style>
  .grant-buttons {
    height: 25px;
    line-height: 15px;
  }
</style>
